import React from 'react';
import { v4 as uuidv4 } from 'uuid';

const ContactList = ({ contacts }) => (
  <ul className="contactList">
    {contacts.map(({ name, number }) => (
      <li key={uuidv4()}>
        <span>{name} </span>
        <span>: {number}</span>
      </li>
    ))}
  </ul>
);

export default ContactList;
